
import Vue from 'vue'

// 

Vue.filter('premiere', (timetamp, hasDay) => {
    let _date = new Date(timetamp)
    let _day = `  ${ formatDay(_date.getDay()) }`
    return `${(_date.getMonth() + 1)}${_date.getDate()}${hasDay ? _day : ''}`
})

function formatDay(day) {
    switch (day) {
        case 0: return '';
        case 1: return '';
        case 2: return '';
        case 3: return '';
        case 4: return '';
        case 5: return '';
        case 6: return '';
    }
}