

import Vue from "vue"
//2018-11-231123,
Vue.filter("time",(time)=>{
    let res = time.split("-");
    var ssdate = new Date(res[0], parseInt(res[1] - 1), res[2]);
    if(res[2]){//
        var  week1 = String(ssdate.getDay()).replace("0","").replace("1","").replace("2","").replace("3","").replace("4","").replace("5","").replace("6","")//
        return  res[1] + "" + res[2] + " " + " " + week1;
    }else{
        return  res[1] + "";
    }
    
})